import { Pipe, PipeTransform } from '@angular/core';
import { IsubscriptionDuration, TsubscriptionDurVal } from '@biz-nest/core-client';

export const subscriptionDurations: IsubscriptionDuration[] = [
  {
    name: 'month',
    value: 1
  },
  {
    name: '2 months',
    value: 2
  },
  {
    name: '3 months',
    value: 3
  },
  {
    name: '6 months',
    value: 6
  },
  {
    name: '1 year',
    value: 12
  }
];
@Pipe({
  name: 'subscriptionDuration',
  standalone: true
})
export class SubscriptionDurationPipe implements PipeTransform {
  /**
   * Given a subscription duration value (e.g. 1, 2, 3, 6, 12), returns the
   * corresponding string name (e.g. "month", "2 months", "3 months", etc.)
   * @param value The value of the subscription duration
   * @returns The string name of the subscription duration
   */
  transform(value: TsubscriptionDurVal): string {
    return subscriptionDurations.find(s => s.value === value)?.name || '';
  }
}
